];
export default {
  name: "modeInfo",
  data() {
    return {
      msg: "",
      bannerArr: bannerArr,
      maskShow : false,
      swiperOption: {
        prevButton: ".swiper-button-prev", //
        nextButton: ".swiper-button-next", //
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        }
      },
      domains: [
        {
          value: ""
        }
      ]
    };
  },
  methods: {
    getContent(value, content) {
      $("#province option").each(function(i, e) {
        if ($(e).val() == value) {
          number = i;
        }
      });
    },
    //
    mask(){
        this.maskShow = true;
    },
    maskHide () {
        this.maskShow = false;
    }
  }
};
</script>

