}
textarea::-webkit-input-placeholder,
input::-webkit-input-placeholder {
  color: #999;
}
textarea::-moz-placeholder,
input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #999;
}
textarea:-moz-placeholder,
input:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #999;
}
textarea:-ms-input-placeholder,
input:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #999;
}

.cityParent {
  position: relative;
}
.select {
  float: left;
  /*divselect*/
  width: 220px;
  height: 40px;
  border-radius: 5px;
  /*,*/
  /*box-shadow: 0 0 5px #ccc;*/
  position: relative;
}
select {
  /*select*/
  border: none;
  /*select*/
  outline: none;
  /*selectdiv*/
  width: 154px;
  height: 40px;
  line-height: 40px;
  font-size: 14px;
  color: #999;
  border: 1px solid #dddddd;
  /* border-radius: 4px; */
  /*select*/
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  /*padding-left*/
  padding-left: 8px;
}
/*select*/
.select:after {
  content: "";
  width: 14px;
  height: 8px;
  background: url(../../img/xia.png) no-repeat center;
  /**/
  position: absolute;
  right: -26px;
  top: 14px;
  /**/
  pointer-events: none;
}
.modeCrumbs {
  font-size: 16px;
  line-height: 48px;
  color: #333333;
  margin-top: 10px;
  padding-left: 15px;
}
.modeInfoLeft {
  float: left;
}
.modeInfoRight {
  float: right;
}
.modeInfoLeft .modeBanner {
  width: 763px;
  height: 607px;
  background: #fff;
  padding: 61px 0 49px 0;
  box-sizing: border-box;
  box-shadow: 0 2px 4px 2px rgba(0, 0, 0, 0.08);
  position: relative;
  margin-bottom: 20px;
}
.modeBanner img {
  width: 100%;
  height: 497px;
  display: block;
}
.modeInfoLeft {
  position: relative;
}

.modeInfoLeft .btnLeft,
.modeInfoLeft .btnRight {
  width: 50px;
  height: 50px;
  position: absolute;
  top: 283px;
  cursor: pointer;
  outline: none;
}
.btnLeft {
  left: 0!important;
  background: url(../../img/prevactive.png) no-repeat!important;
  background-size: 0;
}
.btnLeft:hover {
  background: url(../../img/prev.png) no-repeat;
}
.btnRight {
  right: 0!important;
  background: url(../../img/nextactive.png) no-repeat!important;
  background-size: 0;
}
.btnRight:hover {
  background: url(../../img/next.png) no-repeat;
}
.modeIcon span {
  /* display: block; */
  float: left;
  width: 120px;
  height: 69px;
  margin-right: 10px;
}
.modeIcon img {
  display: block;
  float: left;
  width: 120px;
  height: 70px;
  margin-right: 10px;
}
.modeMsg {
  width: 386px;
  height: 607px;
  background: #fff;
  padding-left: 9px;
  padding-top: 30px;
  box-sizing: border-box;
  box-shadow: 0 2px 4px 2px rgba(0, 0, 0, 0.08);
}
.modeMsg h4 {
  font-size: 22px;
  line-height: 56px;
  padding-left: 20px;
  color: #353535;
}
.modeUserInfo {
  padding-left: 20px;
}
.modeUserTitle {
  height: 70px;
  padding: 13px 0 13px 20px;
  box-sizing: border-box;
  border-top: 1px solid #f6f6f6;
  border-bottom: 1px solid #f6f6f6;
  margin-top: 17px;
}
.modeUserTitle img {
  width: 46px;
  height: 46px;
  border-radius: 23px;
  vertical-align: middle;
}
.modeUserTitle span {
  display: inline-block;
  font-size: 14px;
  line-height: 42px;
  margin-left: 15px;
  vertical-align: middle;
  color: #333333;
}
.modeInfoList {
  padding-left: 28px;
  padding-top: 8px;
  padding-bottom: 11px;
  border-bottom: 1px solid #f6f6f6;
}
.modeInfoList p span {
  font-size: 16px;
  line-height: 32px;
  color: #434343;
}
.describe {
  font-size: 16px;
  line-height: 24px;
  color: #3c3c3c;
  padding-left: 27px;
  margin-top: 25px;
}
.rightBtn {
  padding-right: 10px;
  padding-left: 10px;
  padding-top: 18px;
}
.rightBtn button {
  width: 165px;
  height: 44px;
  font-size: 18px;
  line-height: 44px;
  color: #fff;
  text-align: center;
  padding: 0;
  border: none;
  outline: none;
  text-align: center;
}
.customized{
  float: left;
  color: #fff;
  background: #e6b861;
  border-radius: 0;
}
.transaction {
  float: right;
  background: #ff5f5c;
}
.transactionIcon {
  display: inline-block;
  margin-right: 7px;
  width: 21px;
  height: 20px;
  vertical-align: middle;
  background: url(../../img/downIcon.png) no-repeat;
}
.modeInfoBox .explainBox {
  margin-top: 16px;
  margin-bottom: 42px;
  padding-left: 0;
  padding-bottom: 0;
}
.explain {
  float: left;
  width: 843px;
  height: 270px;
  background: #fff;
  box-shadow: 0 2px 4px 2px rgba(0, 0, 0, 0.08);
  margin-top: 9px;
  font-size: 18px;
  line-height: 48px;
  color: #333333;
}
.explainBox h4 {
  padding-left: 27px;
}
.modeInfoBox .explainBox li {
  font-size: 14px;
  line-height: 32px;
  color: #757575;
  padding-left: 40px;
  background: url(../../img/spot.png) no-repeat 21px 14px;
}
.service {
  float: right;
  margin-top: 9px;
  font-size: 18px;
  line-height: 48px;
  width: 306px;
  height: 270px;
  background: #fff;
  box-shadow: 0 2px 4px 2px rgba(0, 0, 0, 0.08);
}
.service button {
  font-size: 14px;
  line-height: 46px;
  color: #e3aa3f;
  border: none;
  background: transparent;
  outline: none;
  padding: 0;
  margin-left: 40px;
}
.modeIcon .swiper-pagination-bullet {
  background: transparent!important;
  outline: none;
}
.swiper-pagination-bullet-active {
  border: 1px solid #e3aa3f;
  border-radius: 0;
}
.swiper-pagination {
  margin-left: -1px;
}
</style>



